export default {
  data() {
    return {
      loading: true, // 
      queryParams: {}, // 
      tableData: null, // 
      callback: '', // 
      isPage: true,
      checkItems: [], // 
      total: 0
    }
  },
  computed: {
    ids() { // id  id
      const arr = []
      this.checkItems.map(v => {
        arr.push(v.id)
      })
      return arr
    },
    batchStatus() { // 
      if (this.checkItems.length > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    query() { // 
      this.queryParams.page = 1
      this.getTableData(this.callback, this.queryParams)
    },
    /**
     * 
     * @param {Function} callback 
     * @param {Object} params 
     * @param {Boolean} isPage 
     */
    getTableData(callback, params, isPage = true) {
      // this.tableData = []
      this.loading = true
      this.callback = callback
      this.isPage = isPage
      setTimeout(() => {
        callback(params).then(res => {
          if (res.code === 200) {
            if (isPage) {
              this.total = res.data.totalElements
              this.tableData = res.data.content
            } else {
              this.tableData = res.data
            }
          } else {
            this.$errorMessage(res.message)
          }
          this.loading = false
        }).catch(res => {
          this.loading = false
          this.$errorMessage('')
        })
      }, 1)
    },
    getCheckItems(obj) { // 
      this.checkItems = obj
    },
    checkRows(row, event, column) { // 
      this.$refs.dataTable.toggleRowSelection(row)
    },
    getPage(current, size) { // 
      this.queryParams.page = current
      this.queryParams.size = size
      this.getTableData(this.callback, this.queryParams)
    },
    sortNum(el, params, status = false) {
      // 
      return false
    },
    deleteTableData(callback, id, message = '') { // ids
      this.$confirm(message, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        callback({ ids: [id] }).then(res => {
          if (res.code === 200) {
            this.$successMessage('')
            this.getTableData(this.callback, this.queryParams, this.isPage)
          } else {
            this.$errorMessage(',' + res.message)
          }
        })
      })
        .catch(() => {})
    },
    deleteTableDatas(callback, ids, message = '') { // ids
      if (this.batchStatus) {
        this.$confirm(message, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          callback({ ids: ids }).then(res => {
            if (res.code === 200) {
              this.$successMessage('')
              this.getTableData(this.callback, this.queryParams, this.isPage)
            } else {
              this.$errorMessage(',' + res.message)
            }
          })
        })
          .catch(() => {})
      } else {
        this.$errorMessage('')
      }
    }
  }
}



