// 
import axios from '../fetch'

// 
export const productCategoryList = params => { return axios.get('/admin/store/productcategory/getChild', { params: params }) }

export const productCategoryDelete = params => { return axios.post('/admin/store/productcategory/delete', params) }

// 
export const productCategorySave = params => { return axios.post('/admin/store/productcategory/saveChild', params) }

// 
export const productCategoryGet = params => { return axios.get('/admin/store/productcategory/get', { params: params }) }
// 
export const productCategoryUpdate = params => { return axios.post('/admin/store/productcategory/update', params) }

// 
export const productCategoryTree = params => { return axios.get('/admin/store/productcategory/tree', { params: params }) }



