import axios from './../fetch'

// 
export const sysmessPage = params => { return axios.get('/admin/store/message/page', { params: params }) }
// 
export const privatePage = params => { return axios.get('/admin/store/message/privatePage', { params: params }) }
// 
export const delmessage = params => { return axios.post('/admin/store/message/delete', params) }
// 
export const readmessage = params => { return axios.post('/admin/store/message/read', params) }
// 
export const getmessage = params => { return axios.get('/admin/store/message/get', { params: params }) }
// 
export const storeCustomerServiceSave = params => { return axios.post('/admin/store/storecustomerservice/save', params) }
// 
export const storeCustomerServiceGet = params => { return axios.get('/admin/store/storecustomerservice/get', { params: params }) }

// 
export const commentPage = params => { return axios.get('/admin/store/comment/page', { params: params }) }
// 
export const commentGet = params => { return axios.get('/admin/store/comment/get', { params: params }) }
// 
export const commentUpdate = params => { return axios.post('/admin/store/comment/update', params) }

// 
export const consultPage = params => { return axios.get('/admin/store/productconsultation/page', { params: params }) }
// 
export const consultGet = params => { return axios.get('/admin/store/productconsultation/get', { params: params }) }
// 
export const consultUpdate = params => { return axios.post('/admin/store/productconsultation/update', params) }
// 
export const consultAnswer = params => { return axios.post('/admin/store/productconsultation/answer', params) }



