
const Mock = require('mockjs')
export default {
  data() {
    return {
      status: true,
      Mock: Mock,
      loading: false, // 
      type: this.$route.query.type, //   save  update 
      id: this.$route.query.id, // idid
      errorMessage: {}, // 
      timer: null // 

    }
  },
  methods: {
    destroyed() { // 
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    },
    validateForm() {
      // 
      const state = false

      return state
    },
    /** 
     * 
     * @param {Function} dataMethod 
     * @param {Object} data   
     * @param {Boolean} state  
     * @param {String} path   
     * @param {String} type    
     * @param {Object} params  
     */
    saveForm(dataMethod, data, state, path, type, params = {}) {
      return new Promise(resolve => {
        this.$refs.form.validate((valid, item) => {
          if (valid) {
            this.loading = true
            dataMethod(data).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('')
                if (state) {
                  setTimeout(() => {
                    this.rest()
                    this.$routerLink(path, type, params)
                  }, 400)
                } else {
                  this.rest()
                }
              } else if (res.code === 100) { // 
                this.validateError(res.data)
              } else {
                this.$errorMessage(res.code + ':' + res.message)
              }
              resolve(res)
            })
          } else { // 
            let name = ''
            for (const key in item) {
              name = key
              break
            }
            this.errorPosition(name)
          }
        })
      })
    },
    /**
     * 
     * @param {Function} dataMethod 
     * @param {Object} data   
     * @param {String} path   
     * @param {String} type    
     * @param {Object} params  
     */
    updateForm(dataMethod, data, path, type, params = {}, state = true) {
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.loading = true
          dataMethod(data).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$successMessage('')
              if (state) {
                setTimeout(() => {
                  this.rest()
                  this.$routerLink(path, type, params)
                }, 700)
              }
            } else if (res.code === 100) { // 
              this.errorMessage = {}
              this.loading = false
              setTimeout(() => {
                for (const key in res.data) {
                  this.$set(this.errorMessage, key, res.data[key])
                }
              }, 5)
            } else {
              this.loading = false
              this.$errorMessage(res.code + ':' + res.message)
            }
          })
        } else { // 
          let name = ''
          for (const key in item) {
            name = key
            break
          }
          this.errorPosition(name)
        }
      })
    },
    errorPosition(name) {
      // 
      const lay = document.querySelector('#layout')
      if (this.$refs[name]) {
        const ele = this.$refs[name].$el
        lay.scrollTop = ele.offsetTop - 350
      }
      return false
    },
    autoFocus() {
      this.$nextTick(() => {
        this.$refs.form.clearValidate()
      })
    },
    rest() {
      window.clearInterval(this.timer)
      // this.autoFocus()
      this.$nextTick(() => {
        this.$refs.form.resetFields()
        this.destroyed()
      })
    },
    validateError(data) { // 
      this.errorMessage = {}
      setTimeout(() => {
        for (const key in data) {
          this.$set(this.errorMessage, key, data[key])
        }
      }, 1)
    }
  }
}



