// 
import axios from './../fetch'

// 
export const waybillTemplatePage = params => { return axios.get('/admin/store/waybill/page', { params: params }) }
export const waybillTemplateList = params => { return axios.get('/admin/store/waybill/list', { params: params }) }
// 
export const waybillTemplateGet = params => { return axios.get('/admin/store/waybill/get', { params: params }) }

// 
export const waybillTemplateSave = params => { return axios.post('/admin/store/waybill/save', params) }

// 
export const waybillTemplateUpdate = params => { return axios.post('/admin/store/waybill/update', params) }

// 
export const waybillTemplateDelete = params => { return axios.post('/admin/store/waybill/delete', params) }

// 
export const waybillTemplateEnable = params => { return axios.post('/admin/store/waybill/enable', params) }

// 
export const waybillTemplateDisable = params => { return axios.post('/admin/store/waybill/disable', params) }



