// 
import axios from '../fetch'
import qs from 'qs'
// 
export const productPage = params => {
  return axios.get('/admin/store/product/page', { params: params,
    paramsSerializer: function(params) {
      return qs.stringify(params, { arrayFormat: 'repeat' })
    }
  })
}
// 
export const productOffShelve = params => { return axios.post('/admin/store/product/offShelve', params) }

